(function () {
    'use strict';

    angular
        .module('app')
        .controller('AcceptareController', AcceptareController);

    /** @ngInject */
    function AcceptareController(action, item, persoane, firme,  registru, $mdDialog, arrays, qs, gmDialog) {
        var vm = this,
            modal;
        vm.registru = arrays.jsonify(angular.copy(registru));
//        persoane = angular.extend(persoane, firme);
        console.log("peraoane toti:",persoane);
        action == 'add' ? vm.action = "ADAUGARE" : null;
        action == 'edit' ? vm.action = "MODIFICARE" : null;
        vm.acceptare = item;
        console.log("acceptare item: ", item);
        vm.persoane = persoane;

        vm.close = function (item) {
            $mdDialog.hide();
        }
        vm.confirm = function (item) {
           // item.date = moment(item.date).unix();
            $mdDialog.hide(item);
        }

/*        if ( action == "add" ) {
            var lookup = persoane.filter(function(elem) { return elem.name == item.persoana.name; });
            if ( lookup.lenght > 0 ) { 
                item.persoana = lookup[0];
                item.idContact = true;
            }
            
        }*/
        
        console.log("ITEMUL:", item);

        vm.persoana = function (item) {
            modal = $mdDialog.show({
                templateUrl: "app/states/dosar/includes/addAcceptare/person.modal.tpl",
                controller: "PersonController",
                controllerAs: "vm",
                locals: {
                    item: item
                },
                multiple: true
            });
            modal.then(function (r) {
                if (angular.isDefined(r)) {
                    vm.acceptare.persoana = r;
                    vm.acceptare.persoana.isContact = true;
                }
            }, function() {})
        }
        
        vm.switchMandatar = function(item) {
            
            modal = $mdDialog.show({
                templateUrl: "app/states/dosar/includes/addAcceptare/mandatar.modal.tpl",
                controller: "MandatarController",
                controllerAs: "vm",
                clickOutsideToClose: true,
                locals: {persoane:angular.extend(persoane, firme)},
                multiple: true
            })
            
            modal.then( function(r) {
               item.mandatar = r; 
            },  function() {})
        }
        
        vm.addMandatar = function (item) {
            modal = $mdDialog.show({
                templateUrl: "app/states/dosar/includes/addAcceptare/person.modal.tpl",
                controller: "PersonController",
                controllerAs: "vm",
                locals: {
                    item: {name:'', content:{}}
                },
                multiple: true
            });
            modal.then(function (r) {
                if (angular.isDefined(r)) {
                    item.mandatar = {};
                    item.mandatar = r;
               
                }
            }, function() {})
        }        
        
        vm.removeMandatar = function(acceptare) {
            delete(acceptare.mandatar);
        }

        vm.editMandatar = function(item) {

            modal = $mdDialog.show({
                templateUrl: "app/states/dosar/includes/addAcceptare/person.modal.tpl",
                controller: "PersonController",
                controllerAs: "vm",
                locals: {
                    item: item.mandatar
                },
                multiple: true
            });
        }
        
        
        vm.acte = [];
        vm.cloneActe = function() {
          
        vm.acceptare.acte = angular.copy(vm.acte);
        }




    }


})();
(function () {
		'use strict';

		angular
			.module('app')
			.controller('togetherController', togetherController);

		/** @ngInject */
		function togetherController(id, action, msg, convert, localStorageService, qs, gmDialog, $state) {
			var vm = this;

			vm.id = id;
			vm.action = action;
			vm.msg = msg;
			vm.new = {};
			var local = local;
			
			var local = localStorageService.get('user');

		 
			
			
			//	  if the result is a list
			if (msg[0]) { vm.msg = convert.arr2tree(msg); }
			
			if ( action == "add" ) {
				vm.new.parent = local.uid;
				vm.new.sender = local.uid;
				vm.new.senderName = local.user.surname + ' ' + local.user.name;
				vm.new.recepient = 6; // always admin
				vm.new.recepientName = "Gabriel Maftei";
				vm.new.subject = '';
				vm.new.content = '';
				vm.new.date = moment().unix();
				vm.new.status = 'active';
				vm.new.accessType = 'private';
				
			}

			if ( action == "reply" ) { 
				
				vm.new.parent = vm.msg.id;
				vm.new.sender = local.uid;
				vm.new.senderName = local.user.username;
				vm.new.recepient = vm.msg.sender;
				vm.new.recepientName = vm.msg.senderName;
				vm.new.subject = 'Rspuns la: ' + vm.msg.subject;
				vm.new.content = vm.msg.senderName + "a scris:" + vm.msg.content + "<hr><br><b>Rspunsul Dvs:</b><br>";
				vm.new.date = moment().unix();
				vm.new.status = 'active';
				vm.new.accessType = 'private';
			
			}
			
			vm.confirm = function(data) {
				console.log(data);
				qs.create('mesagerie', data)
				.then(function(r) { 
					gmDialog.alert("SUCCES!", "Mesajul Dvs. a fost transmis!", "NCHIDE")
					.then(function() {
						$state.go('index.together', {action:'list', msgId:''});
					})
					
				})
			}
			
			vm.remove = function(data) {
				qs.remove('mesagerie', {id:data.id})
				
				.then(function(r) {
					
						$state.reload();
					}, function(er) { 
							if ( er.status == '406') {
								alert('can only delete own resources!');
							}
				})
					
			
			}
			


		}


	})();
(function () {
    'use strict';
    angular
        .module('app')
        .config(routerConfig);
    /** @ngInject */
    function routerConfig($stateProvider) {
        $stateProvider
        .state('index.together', {
            url: '/together/:action/:msgId',
            views: {
                'content@index': {
                    templateUrl: function($stateParams) {
						var action = $stateParams.action, id = $stateParams.msgId;
						if ( action == "list" ) { return "app/states/together/main.all.tpl"; }
						if ( $stateParams.action == "add"  && id == '' ) { return "app/states/together/main.add.tpl"; }
						if ( $stateParams.action == "edit" && id != '' ) { return "app/states/together/main.edit.tpl"; }
						if ( $stateParams.action == "reply" && id != '') { return "app/states/together/main.reply.tpl"; }
					},
                    controller: 'togetherController',
                    controllerAs: 'vm',
                    resolve: {
						id: function($stateParams) { return $stateParams.msgId; },
						action: function($stateParams) { return $stateParams.action; },
                        msg: function( qs, $stateParams ) {
							
							if ( $stateParams.msgId == '' ) {
								return qs.get('mesagerie').then(function(response){ return response.data.result || null; });
							} else {
								return qs.get('mesagerie', {key:'id', value: $stateParams.msgId})
									.then(function(response){ return response.data.result[0] || null; })		
							}								
                           
                           
                        }
                    }, 
                }
            }
        })
    }
})();
