(function () {
	'use strict';

	angular
		.module('app')
		.controller('settingsCtrl', settingsCtrl);

	/** @ngInject */
	function settingsCtrl(setari, tipDoc, logos, qs, $q, gmDialog, localStorageService, $http, $sce, Upload, nrManual, publicModule, $window) {
		var vm = this;
        vm.clearCache = function() {
            location.reload(true);
        }
        vm.setari = setari;
		vm.docs = tipDoc.get();
		vm.defaultOption = 0;
        vm.local = localStorageService.get('user');
		
		vm.locals = localStorageService.get('user');

		vm.titulaturi = setari.filter(function (e) { return e.prop == "semnatura"  });
        
        //userul curent (titulatura)
        vm.currentUser = vm.setari.filter(function(e) { return e.prop == "currentUser"})[0] || {};
        vm.currentUser.val = vm.currentUser.val || vm.local.user.name + ' ' + vm.local.user.surname;
        vm.currentUser.prop = "currentUser";
             console.log("curattr:", vm.currentUser.attr);       
        //UPDATE OR CREATE TITULATURA PENTRU CURRENT USER
        vm.updateCurrentUser = function() {
console.log("curattr:", vm.currentUser.attr);
            if ( typeof vm.currentUser.id === "undefined" ) {
                qs.create('configurari', vm.currentUser)
                .then(function(resp) {
                    vm.currentUser.id = resp.data.msg;
                } )
            } else { qs.update('configurari', vm.currentUser) }
            
        }
        
        
		vm.semnaturi = setari.filter(function (e)  { return e.prop == "semnaturi"  });
		angular.forEach(vm.semnaturi, function (el, ix) {
			vm.semnaturi[ix].val = JSON.parse(el.val);
		})
		
		vm.numarManual = ( typeof nrManual != "undefined" && nrManual.val == true) ? true : false;

		
		vm.updateNumarManual = function(state) {

			var nrm = {prop:"numarManual", val: state};
			if ( typeof nrManual === "undefined" ) {
				qs.create('configurari', nrm)
				.then(function(r) { 
					nrm.id == r.data.msg; 
					nrManual = nrm;
				});
			} else {
				nrManual.val = state;
				qs.update('configurari', nrManual);
			}
			
		}
		

		vm.createTitulatura = function () {
			var titulatura = {
				prop: "semnatura",
				attr: "",
				val: ""
			};
			qs.create('configurari', titulatura)
				.then(function (r) {
					titulatura.id = r.data.msg;
					console.log(titulatura);
					vm.titulaturi.push(titulatura);
				})
		}

		vm.updateTitulatura = function (el) {
			qs.update('configurari', el);
		}

		vm.removeTitulatura = function (el) {
			console.log(el.id);
			qs.remove('configurari', el.id)
				.then(function (r) {
					vm.titulaturi.splice(vm.titulaturi.indexOf(el), 1);
				})
		}



		vm.addSemnatura  = function(attr) {
			var doc = attr.split('.').splice(0, 1)[0].split('/')[attr.split('.').splice(0, 1)[0].split('/').length - 1];
			var targetRow = vm.semnaturi.filter(function(e) { return e.attr == doc; })[0];
			//create target if it doesn't exist
			typeof targetRow === "undefined" ? targetRow = {prop:"semnaturi", attr:doc, val:[]} : targetRow = targetRow;
			//get max ordering
			var max = 0; 
			angular.forEach(targetRow.val, function(e,i) {
				e.ordering > max ? max = e.ordering : max = max;
			});
			targetRow.val.push({id:null, ordering:max+1});
			var postdata = angular.copy(targetRow);
			postdata.val = JSON.stringify(targetRow.val);
			if (typeof postdata.id !== "undefined") {
				qs.update('configurari', postdata);
			} else {
				qs.create('configurari', postdata)
				.then(function(r) {
					targetRow.id = r.data.msg;
					vm.semnaturi.push(targetRow);
				})
			}
			
		}
		
		vm.updateSemnatura = function(attr) {
			var targetRow = vm.semnaturi.filter(function(e) { return e.attr == attr; })[0];
			var postdata = angular.copy(targetRow);
			postdata.val = JSON.stringify(postdata.val);
			qs.update('configurari', postdata);
		}
		
		vm.removeSemnatura = function(doc, el) {
			
			var target = vm.semnaturi.filter(function(e) { return e.attr == doc.attr; })[0];
			console.log("init target", target);

			var postdata = angular.copy(target);
			postdata.val.splice(postdata.val.indexOf(el),1);
			postdata.val = JSON.stringify(postdata.val);
			qs.update('configurari', postdata)
			.then(function() {
				target.val.splice(target.val.indexOf(el),1);
			})
			
			
		}
		
		
		//PUBLIC API ACTIVATION ==================================================================
		publicModule.val = angular.fromJson(publicModule.val);
		vm.publicModule = publicModule;
		console.log(vm.publicModule);
	
		
		vm.updatePublicModule = function() {
			var postdata = angular.copy(publicModule);
			
			postdata.val = angular.toJson(postdata.val);
			if ( typeof postdata.id === "undefined") {
				qs.create('configurari', postdata);
			} else {
				qs.update('configurari', postdata);
			}
		}
		
		// generate api endpoint
		vm.generateLink = function(url) { 
			if ( url.length > 5) {
				vm.publicModule.attr = true;
				vm.publicModule.val.endpoint = "https://avizier.agricole.ro/#/request/" + (parseInt(vm.locals.company.id)*18012010).toString(36);	
				console.log(vm.publicModule);			
			} else { 
				vm.publicModule.attr = false;
				vm.publicModule.val.endpoint = '';}

		 	
		}
		
		//copy link to clipboard 
		vm.copyToClipboard = function(link) {
			console.log(link);
			var body = angular.element($window.document.body);
			var textarea = angular.element('<textarea/>');
			textarea.css({ position: 'fixed', opacity: '0' });
			textarea.val(link);
			body.append(textarea);
			textarea[0].select();	
			try {
				var successful = document.execCommand('copy');
				if (!successful) throw successful;
			} catch (err) {
				window.prompt("Copy to clipboard: Ctrl+C, Enter", toCopy);
			}
			textarea.remove();			
		}
		
		// test url
		
		vm.testUrl = function(url) {
			var ln = $sce.trustAsResourceUrl(url);
			$http.get(ln)
				.then( function( resp ) { console.log ( "then", resp );})
				.catch( function( resp ) { console.log ( "catch", resp );})
		}
		
		
		// SIGLE=======================================================
		vm.logos = logos;
		console.log(vm.logos);
		vm.logo = {};
		vm.logo.left = vm.logos.filter(function(item) { return item.layout == "left"})[0] || {layout:'left'};
		vm.logo.center = vm.logos.filter(function(item) { return item.layout == "center"})[0] || {layout:'center'};
		vm.logo.right = vm.logos.filter(function(item) { return item.layout == "right"})[0] || {layout:'right'}

		vm.upload = function (file, position) {
			var fileReader = new FileReader();
			fileReader.readAsDataURL(file);
			fileReader.onload = function (e) {
				var dataUrl = e.target.result;
				vm.logo[position].img = dataUrl;
			};
		};
		
		vm.removeLogo = function(layout) {
			console.log(layout);
			vm.logo[layout].img = null;
		}
	
		vm.updateLogos =  function() { 

			var prms = [], q1, q2, q3;

			
			if ( typeof vm.logo.left.id != "undefined") {
				q1 = qs.remove('sigle', vm.logo.left.id)
				.then(function() {
					qs.create('sigle', vm.logo.left)
				})
			} else { q1 = qs.create('sigle', vm.logo.left) }
			
			if ( typeof vm.logo.center.id != "undefined") {
				q2 = qs.remove('sigle', vm.logo.center.id)
				.then(function() {
					qs.create('sigle', vm.logo.center)
				})
			} else { q2 = qs.create('sigle', vm.logo.center) }		
			
			if ( typeof vm.logo.right.id != "undefined") {
				q2 = qs.remove('sigle', vm.logo.right.id)
				.then(function() {
					qs.create('sigle', vm.logo.right)
				})
			} else { q2 = qs.create('sigle', vm.logo.right) }	
			
			prms = [q1,q2,q3];
			
			$q.all(prms).then(function() { gmDialog.alert("SUCCESS", "Actualizarea logo-urilor a fost realizat!", "NCHIDE")});
		}
		
		// END SIGLE ==================================================
		
		
		
		vm.confirma = function() {
			var semn = angular.copy(vm.semnaturi);
			var promise = [];
			angular.forEach(semn, function(e,i) {
				e.val = JSON.stringify(e.val);
				//update
				if (typeof e.id !== "undefined") {
					var q1 = qs.update('configurari', e);
					promise.push(q1);
				} else {
					//create
					var q2 = qs.create('configurari', e);
					promise.push(q2);
				}
				
			
				
			});
			$q.all(promise).then(function() { gmDialog.alert("SUCCES!", "Actualizarea a fost realizat cu succes!", "NCHIDE"); })
		}




	}


})();
(function () {
    'use strict';

    angular
        .module('app')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider) {

        $stateProvider
        .state('index.settings', {
            url: '/settings',
            views: {
                'content@index': {
                    templateUrl: "app/states/settings/settings.html",
                    controller: 'settingsCtrl',
                    controllerAs: 'vm',
					resolve: {
						setari: function( qs, localStorageService ) {
							return qs.get('configurari').then(function(r) { return r.data.result; });
						},
						nrManual: function(setari, localStorageService) {
							var uid = localStorageService.get('user').uid;
							return setari.filter(function(r) { return (r.prop == "numarManual" && r.uId == uid); })[0];
						},
						publicModule: function(setari, localStorageService ) {
							var uid = localStorageService.get('user').uid;
							return setari.filter(function(r) { 
								return (r.prop == "publicModule" && r.uId == uid) })[0] 
								|| {prop:'publicModule', val:{referrer:'https://', endpoint:''}};
							
						},
						logos: function(qs) {
							return qs.get('sigle').then(function(r) { 
								return r.data.result || [{layout:'left', img:null}, 
														 {layout:'center', img:null}, 
														 {layout:'right', img:null}]; 
							})
						}
						
					}
               
                }
            }
            
        })
        
    }

})();
(function () {
	'use strict';

	angular
		.module('app')
		.controller('printRegistruControlller', printRegistruControlller);

	/** @ngInject */
	function printRegistruControlller(registru, $mdDialog) {

		var vm = this;

		vm.registru = angular.copy(registru);

		vm.close = function () {
			$mdDialog.hide();
		}

		vm.filter = {
			active: false
		};
		vm.doFilter = function (state) {
			console.log(state);

			switch (state) {
				case 'swap':
					// activare filtru
					if (vm.filter.active == false) {
						vm.filter.active = true;
						vm.filter.startDate = moment().startOf('year').toDate();
						vm.filter.endDate = moment().toDate();
					}
					//dezactivare filtru
					else if (vm.filter.active == true) {
						vm.filter = {};
						vm.filter.active = false;
						vm.registru = angular.copy(registru);
					}
					break;
				case 'start':
					// start date filtering
					var reg = angular.copy(registru);
					vm.registru = reg.filter(function (item) {

						var regDate = moment(item.date);
						var startDate = moment(vm.filter.startDate);
						return startDate <= regDate;
					})
					break;
				case 'end':
					// end date filtering
					var reg = angular.copy(registru);
					vm.registru = reg.filter(function (item) {
						var regDate = moment(item.date);
						var endDate = moment(vm.filter.endDate);
						return endDate >= regDate;
					})
					break;					

			}







		}


	}


})();
