(function() {
  'use strict';

  angular
    .module('app')
    .controller('RegistruController', RegistruController);

  /** @ngInject */  
  function RegistruController( registru, qs, gmDialog, $mdDialog, $mdMenu ) {
      var vm = this;

	  

	  
      var scadenta, zileRamase, now = moment(new Date());
      
      angular.forEach(registru, function(e,i) {

          registru[i].date = new Date(e.date*1000+0);
          console.log(e.date);
          
          scadenta = moment(e.date).add(30,'days').toDate(); 
         
          zileRamase = moment.duration(moment(scadenta).diff(now)).days();
          
          
         // scadenta = scadenta.add(30, "days");
          registru[i].scadenta = scadenta;
          registru[i].zileRamase = zileRamase;
          registru[i].firma = JSON.parse(e.firma);
          if ( e.persoane.length > 0  ) { registru[i].persoane = JSON.parse( e.persoane ); }
          registru[i].content = JSON.parse( e.content );
        

      });
      
      vm.registru = registru;
      console.log(vm.registru);
/*      vm.confirm = function(item) {
          qs.update('apps', item)
            .then(function(r) {
              gmDialog.alert("SUCCES!", "Ati actualizat cu succes aplicatia "+item.appName, "NCHIDE" )
          })
      }*/
	  
	  


	  vm.printRegistru = function() {
		  console.log("print");
		  var modal = $mdDialog.show({
			  templateUrl: "app/states/registru/print.registru.html",
			  controller: "printRegistruControlller",
			  controllerAs: "vm",
			  locals: {
				  registru: registru
			  },
			  clickOutsideToClose: true
		  });
	  }

      
      vm.options = function(data) {
          var modal = $mdDialog.show({
              targetEvent: data,
              controller: "RegOptionsCtrl",
              controllerAs: "vm",
              templateUrl: "app/states/registru/RegOptionsModal.html",
              clickOutsideToClose: true,
              locals: {
                  data: data
              }
          });
          modal.then(function(r) {
              if (r) {
                  if ( r.action == "remove" ) {  remove(r.data); }
                  if ( r.action == "clone" ) {  clone(r.data); }
                  if ( r.action == "suspend" ) {  suspend(r.data); }
                  
              }

          })
      }
      // comes from modal (next 3 functions should be but inside a service :))) )
      function remove(data) {
          var id = data.id;
          gmDialog.prompt(id, "ATENIE!", "Se va terge dosarul cu toate documentele acestuia IRECUPERABIL! Scriei DA (sau IGEN) dac suntei sigur",  "DA", "NU")
          .then(function(r) {
              if ( r.toUpperCase() == 'DA' || r.toUpperCase() == 'IGEN') {
                  var deletedIndex = registru.filter(function(e) { return e.id == id;})[0];
                  qs.remove('registru', id)
                    .then(function(resp) {
                      if ( resp.data.ok == true ) {
                          gmDialog.alert("SUCCES", "stergerea a fost realizat!", "NCHIDE");
                          registru.splice(registru.indexOf(deletedIndex),1);
                      }
                  })
              }
          })
      }
      
      function clone(data){}
      
      function suspend(data) {
          gmDialog.alert("METOD NEIMPLEMENTAT", "aceast opiune urmeaz s fie implemntat n urmtoarea versiune.", "AM NELES");
      }
	  

   
  
  }
    

})();

