(function () {
    'use strict';

    angular
        .module('app')
        .controller('dosarNouController', dosarNouController);

    /** @ngInject */
    function dosarNouController(qs, $mdDialog, gmDialog, persoane, registru, $sce, $state, arrays) {
        var vm = this, modal;
        
        vm.dosar = {};
        vm.dosar.nr = null;
        vm.dosar.date = new Date();

        vm.persoane = [{titular:{}, mandatar:{}}];
        
        vm.firma = {};
        vm.firma.mandatar  = {};
        vm.firma.mandat = {};
        
        vm.cerere = {};
        vm.cerere.preemptori = [];
        vm.cerere.acte = [];
        
        var numereDosare = [];
        angular.forEach(registru, function(e) {
            numereDosare.push(parseInt(e.nr));
        })

        
        //get last dosar number
        var lastDosarNumber = 0;
        angular.forEach(numereDosare, function(e) {
            e >lastDosarNumber ? lastDosarNumber = e : lastDosarNumber = lastDosarNumber;
        })
        vm.dosar.nr = lastDosarNumber+1;
       
        
        //check dosar existance
       
        vm.checkDosarNumber = function(item) {
            if (numereDosare.includes(parseInt(item))) {
                var nextDosarNumber = lastDosarNumber+1;
                
                var confirm = $mdDialog.confirm()
                    .targetEvent(item)
                    .title('ATENIE')
                    .textContent('Dosarul cu numrul ' + item + ' exist deja! Dorii s folosii urmtorul numr disponibil, adic ' + nextDosarNumber + ' ?')
                    .ariaLabel()
                    .ok('DA')
                    .cancel('NU');
                $mdDialog.show(confirm)
                    .then(function() {
                        vm.dosar.nr = nextDosarNumber;
                    })
                // "FINALLY", SOLUTIA PULLLLIIIII!!!! PENTRU CA NU REZOLVA REJECTUL PROMIS-ului
                    .finally(function() { 
                       vm.dosar.nr != nextDosarNumber ? vm.dosar.nr = null : vm.dosar.nr = vm.dosar.nr;
                      
                    })
  
            }
        }
        
        

    
        //get md-select data (vm.titulari, vm.firme)
        
        vm.titulari = persoane.filter(function(item) {  return item.cui.length == 0;   });
      
        //parse titular.content
        angular.forEach(vm.titulari, function(e,i) {  vm.titulari[i].content = JSON.parse(e.content);  })

      
        vm.firme = persoane.filter(function(item) {   return item.cui.length > 0;   });
        angular.forEach(vm.firme, function(e,i) {
            if (e.content == '') {
                e.content = {};
            } else {
                vm.firme[i].content = JSON.parse(e.content);
            }
        })
        
        // end get md-select data

// init objects on switch tip persoana
        vm.tipPersoana = false;
        vm.switch = function() {
            
            vm.tipPersoana = !vm.tipPersoana;
            console.log(vm.tipPersoana);
            vm.firma = {};
            vm.persoane = [{titular:{}, mandatar:{}}];
        }
        
        
//persoana
        vm.addPersoana = function () {
           
            vm.persoane.push({titular:'', mandatar:''});
        }

        vm.rmPersoana = function (item) {
            var idx = vm.persoane.indexOf(item);
            vm.persoane.splice(idx, 1);
        }

        

        
//ADD NEW PERSOANA (MODAL)
        vm.addNewPersoana = function(index, tip) {
            modal = null;
            modal = $mdDialog.show({
                parent: modal, 
                targetEvent: index,
                templateUrl: 'app/modals/addPersModalTpl.html',                 
                controller: 'addPersModalCtrl',
                controllerAs: 'vm'
            });
            
            modal.then(function(r) {
                if ( typeof r != 'undefined') {
                     qs.get('persoane', {key:'id', value: r}).then(function(resp) {
                         var newPers = resp.data.result[0];
                         angular.isUndefined(newPers.content) ? newPers.content = {} : null;
                         newPers.content = angular.fromJson(newPers.content);
                         vm.titulari.push(newPers);
                         vm.persoane[index][tip] = vm.titulari[vm.titulari.indexOf(newPers)];
                    })               
                }
            }, function() { console.log('click outside')})
        }
 // EDIT PERSOANA (MODAL)       
        vm.editPersoana = function(index, ev, tip) {
            modal = null;
            modal = $mdDialog.show({
                parent: modal,
                targetEvent: ev, 
                templateUrl: "app/modals/editPersModal.html",
                controller: "editPersModalCtrl",
                controllerAs: "vm",
                locals: { titular: ev, tip: tip }
            });
            modal.then(function(r) {
                if ( typeof r !== "undefined" ) {
                     qs.get('persoane', {key:'id', value: r}).then(function(resp) { 
                         var curPers = resp.data.result[0];
                         curPers.content = JSON.parse(curPers.content);
                         vm.titulari[index][tip] = curPers;
                    })                     
                }
            })
        }
        

      
 // add new FIRMA
        vm.addNewFirma = function(ev) {
            modal = null;
            modal = $mdDialog.show({
                parent: modal, 
                targetEvent: ev,
                templateUrl: 'app/modals/addFirmaModalTpl.html',                 
                controller: 'addFirmaModalCtrl',
                controllerAs: 'vm'
            });
            
            modal.then(function(r) {
                qs.get('persoane').then(function(res) { 
                    var firme = res.data.result;
                    vm.firme = firme.filter( function(e) { return e.cui.length > 0 } );
                    vm.firma = vm.firme.filter( function(e) { return e.id == r; } )[0];
                })
            })
        } 
//EDIT FIRMA        
        vm.editFirmaModal = function(ev) {
            modal = null;
            modal = $mdDialog.show({
                parent: modal,
                targetEvent: ev,
                templateUrl: "app/modals/editFirmaModalTpl.html",
                controller: "editFirmaModalCtrl",
                controllerAs: "vm",
                locals: { firma: ev }
            });
            modal.then(function(r) {
                if ( typeof r !== "undefined" ) {
                    qs.get('persoane').then(function(res) { 
                        var firme = res.data.result;
                        vm.firme = firme.filter( function(e) { return e.cui.length > 0 } );
                        vm.firma = vm.firme.filter( function(e) { return e.id == r; } )[0];
                        vm.firma.content = JSON.parse(vm.firma.content);
                    })                    
                }
            })
        }      

// ADD NEW DELEGAT
        vm.addDelegat = function(ev) {
            modal = null;
            modal = $mdDialog.show({
                parent: modal, 
                targetEvent: ev, 
                templateUrl: "app/modals/addPersModalTpl.html",
                controller: "addPersModalCtrl",
                controllerAs: "vm"
            });
            
            modal.then(function(r) {
                console.log(r); // id of newly inserted contact
                qs.get('persoane').then( function( resp ) {
                    vm.titulari = arrays.jsonify(resp.data.result);
                    var idx = vm.titulari.filter( function(elem) { return elem.id == r });
                    idx.length > 0 ? idx = vm.titulari.indexOf(pers[0]) : null;
                    vm.firma.mandatar = vm.titulari[idx];
                    
                    
                })
            }) 
        }
//EDIT NEW DELEGAT
        vm.editDelegat = function(ev) {
            
            modal = null;
            modal = $mdDialog.show({
                parent: modal, 
                targetEvent: ev, 
                templateUrl: "app/modals/editPersModal.html",
                controller: "editPersModalCtrl",
                controllerAs: "vm",
                locals: { titular: ev, tip: 'delegat'}
            });            
        }
        
         
        vm.submit = function() {
            var postData = angular.copy(vm.dosar);
            
            
            postData.date      = moment(postData.date).unix();
            
            if (vm.tipPersoana == true) {
                delete(postData.persoane);
                console.log("F I R M A: ",vm.firma);
                
                postData.firma     = angular.toJson(vm.firma);

            } else {
                delete(postData.firma);
                postData.persoane  = angular.toJson(vm.persoane);
            }

            
            postData.content = postData.content ? postData.content : {};
            postData.content.cerere = angular.toJson(postData.content.cerere);
            
            postData.content = angular.toJson(postData.content);
            
            
            
            qs.create('registru', postData)
            .then(function(r) {
                console.log(r);
                if (r.data.ok == true) {
                    gmDialog.alert('SUCCES!', 'Cererea a fost adugat cu succes!', 'OK')
                    .then(function() {
                        $state.go('index.dosar', {id: r.data.msg});   
                    })
                }
            })
        }
        
        
    }


})();
