(function () {
    'use strict';

    angular
        .module('app')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider) {

        $stateProvider
        .state('index.clona', {
            url: '/clona',
            views: {
                'content@index': {
                    templateUrl: "app/states/clona/clona.tpl",
                    controller: 'ClonaController',
                    controllerAs: 'vm', 
                    resolve: {
                        registru: function( qs ) {
                            return qs.get('registru')
							.then(function(r) { 
                                var res = r.data.result;
                                angular.forEach(res, function(e,i) {
                                    res[i].firma = (e.firma == null) ? e.firma : JSON.parse(e.firma);
                                    res[i].persoane = (e.persoane.length <1) ? e.persoane : JSON.parse(e.persoane);
                                    res[i].content = JSON.parse(e.content);
                                })
                                return res; })
                        }

                    }
               
                }
            }
            
        })
        
      
    }

})();
(function() {
  'use strict';

  angular
    .module('app')
    .controller('ClonaController', ClonaController);

  /** @ngInject */
  function ClonaController( registru, qs, $state ) {
      var vm = this;
     
	 vm.registru = registru;
      
      vm.willClone = [
          {
              elem: "cerere",
              label: "CEREREA",
              disabled: true,
              hidden: false,
              checked: true
        },
          {
              elem: "oferta",
              label: "OFERTA",
        },
          {
              elem: "listaPreemptori",
              label: "LISTA PREEMPTORILOR",
        },
          {
              elem: "acceptari",
              label: "ACCEPTRILE DE COMUNICARE",

        }
      ];
      
     var clonedContent = {};
    var elem;
  
      vm.checkbox = function(ev, val)  {
          console.log(ev, val);
          if ( val == true ) {
              clonedContent[ev] = angular.copy(vm.selectedDosar.content[ev]);
          }
          if ( val == false ) {
              delete(clonedContent[ev]);
          }
          //succesion logics
          //checking....
          if ( val == true && ev == "listaPreemptori") {
              clonedContent['oferta'] = angular.copy(vm.selectedDosar.content['oferta']);
              elem = vm.willClone.filter(function(e) { return e.elem == "oferta"})[0];
              elem.checked  = true;
          }
            if ( val == true && ev == "acceptari") {
              clonedContent['oferta'] = angular.copy(vm.selectedDosar.content['oferta']);
               elem = vm.willClone.filter(function(e) { return e.elem == "oferta"})[0];
               elem.checked  = true;
              clonedContent['listaPreemptori'] = angular.copy(vm.selectedDosar.content['listaPreemptori']);
               elem = vm.willClone.filter(function(e) { return e.elem == "listaPreemptori"})[0];
               elem.checked = true;
          }   
          //unchecking...
            if (val == false && ev == "oferta") {
                elem = vm.willClone.filter(function(e) { return e.elem == "listaPreemptori"})[0];
                if (typeof elem !== "undefined") { 
                    elem.checked = false;
                    delete(clonedContent['listaPreemptori']);
                }

                elem = vm.willClone.filter(function(e) { return e.elem == "acceptari"})[0];
                if (typeof elem !== "undefined") {
                    elem.checked = false;
                    delete(clonedContent['acceptari']);
                }
                
            }
      }
      
      vm.selectDosar = function(dosar) {
          vm.minDate = moment(dosar.date*1000).toDate();
          vm.newDosar = {};
          vm.newDosar.date = new Date();    
          
          var max = 0;
          angular.forEach(registru, function(e,i){
              parseInt(e.nr) > max ? max = parseInt(e.nr) : max=max;
          })
          vm.newDosar.nr = max+1;
          
           // search if docs exist in dosar
          var content = vm.selectedDosar.content;
          
          angular.forEach(vm.willClone, function(e,i) {
                
              if (typeof content[e.elem] === "undefined") {
                  vm.willClone[i].disabled = true;
              } else {
                  vm.willClone[i].checked = true;
                  clonedContent[e.elem] = angular.copy(content[e.elem]);
              }
              
          })
          console.log(clonedContent);
          
          
      }

      
      vm.cloneaza = function() {
          var cloned = [];
          cloned = angular.copy(vm.selectedDosar);
          
          cloned.nr = vm.newDosar.nr;
          cloned.date = moment(vm.newDosar.date).unix();

          cloned.content = clonedContent;
                   
        // remove unnecesary post data
          delete(cloned.id);
          delete(cloned.cId);
          delete(cloned.uId);
          delete(cloned.accessType);
          cloned = stringifica(cloned);
           console.log("clona:",cloned);
          
          qs.create('registru', cloned)
          .then(function(r){
              var id = r.data.msg;
              $state.go('index.dosar', {id:id});
          })
      }
      
      
      
         //functions (should be moved into a service)
           

            
              
        
            
            function stringifica(obj) {
                angular.forEach(obj, function(val,key) {
                    if (typeof val == "object") {
                        obj[key] = JSON.stringify(val);
                        stringifica(val);
                    }
                   
                })
                return obj;
                
            }       
     
  }
    

})();
