(function() {
  'use strict';

  angular
      .module('app')
      .service('isValid', isValid);

  /** @ngInject */
  function isValid() {

    
      
    this.cnp = cnp;


	// cnp validation
    function cnp(p_cnp) {
        var result = {};
        var i = 0,
            year = 0,
            hashResult = 0,
            cnp = [],
            hashTable = [2, 7, 9, 1, 4, 6, 3, 5, 8, 2, 7, 9];
        if (p_cnp.length !== 13) {
            result.ok = false;
            result.err = "Lungime incorect";
            return result;
        }
        for (i = 0; i < 13; i++) {
            cnp[i] = parseInt(p_cnp.charAt(i), 10);
            if (isNaN(cnp[i])) {
                
                result.ok = false;
                result.err = "CNP trebuie s fie numeric!";
                return result;
            }
            if (i < 12) {
                hashResult = hashResult + (cnp[i] * hashTable[i]);
            }
        }
        hashResult = hashResult % 11;
        if (hashResult === 10) {
            hashResult = 1;
        }
        year = (cnp[1] * 10) + cnp[2];
        switch (cnp[0]) {
        case 1:
        case 2:
            {
                year += 1900;
            }
            break;
        case 3:
        case 4:
            {
                year += 1800;
            }
            break;
        case 5:
        case 6:
            {
                year += 2000;
            }
            break;
        case 7:
        case 8:
        case 9:
            {
                year += 2000;
                if (year > (parseInt(new Date().getYear(), 10) - 14)) {
                    year -= 100;
                }
            }
            break;
        default:
            {
                return false;
            }
        }
        if (year < 1800 || year > 2099) {
            return false;
        }
        return (cnp[12] === hashResult);

    }
    
          
  }

})();

