(function() {
  'use strict';

  angular
      .module('app')
      .service('arrays', arrays);

  /** @ngInject */
  function arrays() {

    this.max = max;
    this.min = min;
    this.closest = closest;
    this.lookup = lookup;
    this.jsonify = jsonify;
    this.isJson = isJSON;


    function max(arr, elem) {
        var item = {};
        item[elem] = 0;
        for ( var i = 0; i < arr.length; i++ ) {
            var e = arr[i];
            if ( parseFloat(e[elem]) > item[elem] ) { item = e; }
        } 
        return item;
    }

    function min(arr, elem) {
        var item = arr[0];
        for ( var i = 0; i < arr.length; i++ ) {
            var e = arr[i];
            if ( e[elem] < item[elem] ) { item = e; }
        } 
        return item;
    }
      
    function closest(arr, attr, value, direction) {
       //our target item
        value = parseInt(value);
        var selectItem = arr.filter(function(e) { return e[attr] == value; })[0];
        var selectItemValue = parseInt( selectItem[attr] );
        var ecart = 0;
        if (direction == 'before') {
            arr = arr.filter(function(e) { return parseInt(e[attr]) < value; });
        } 
        else if ( direction == 'after' ) {
             arr = arr.filter(function(e) { return parseInt(e[attr]) > value; });
        }
            for ( var i = 0; i< arr.length; i++ ) {
                var test = arr[i];
                var testValue = parseInt( test[attr] );
                var newEcart = Math.abs(selectItemValue - testValue);
                if ( ecart == 0 || newEcart < ecart ) {
                    selectItem = test;
                    ecart = newEcart;
                }
            }
            return selectItem;
    } 
      
    function lookup(arr, elem, val) {
        if (!angular.isArray(arr)) { return "param 1 is not an array"; }
        if (!angular.isString(val)) { return "param 3 in not a string"}
        var result = false, results;
        results = arr.filter( function(e) { return e[elem] == val});
        if ( !angular.isUndefined(results) && results.length > 0 ) {
            return true;
        } else { return false;}
        
    }
      
// jsonify stufff
        function jsonify(arr) {
          if ( angular.isObject(arr)) {
            angular.forEach(arr, function(e,i) {
              if ( isJSON(e) ) { 
                arr[i] = angular.fromJson(e);
                jsonify(arr[i]);
              } else {
                jsonify(arr[i]);
              }

            })

          }
          return arr;
        } 

        function isJSON(data) {
            var isJson = false
            try {
                // this works with JSON string and JSON object, not sure about others
                var json = angular.fromJson(data);
                isJson = angular.isObject(json);
            } catch (ex) {
                isJson =  false
            }
            return isJson;
        }  

// end jsonify stufff    
      

      
  }

})();

