(function () {
    'use strict';

angular
	.module('app')
    .factory('noCacheInterceptor')
    
    
    function noCacheInterceptor($log, $injector) {
        var noCacheFolders = ["views", "scripts", "lib"];

                    var shouldApplyNoCache = function (config) {
                        // The logic in here can be anything you like, filtering on
                        // the HTTP method, the URL, even the request body etc.
                        for (var i = 0; i < noCacheFolders.length; i++) {
                            var folder = noCacheFolders[i];
                            if (config.url.indexOf(folder + "/") === 0) {
                                return true;
                            }
                        }
                        return false;
                    };

                    var applyNoCache = function (config) {
                        if (config.url.indexOf("?") !== -1) {
                            config.url += "&";
                        } else {
                            config.url += "?";
                        }
                        config.url += "nocache=" + new Date().getTime();
                    };

                    var interceptor = {
                        request: function (config) {
                            if (shouldApplyNoCache(config)) {
                                applyNoCache(config);
                            }
                            return config;
                        }
                    };

                    return interceptor;   
    
    }
    
})()
(function () {
    'use strict';

    angular
        .module('app')
        .directive('dz', dzDirective)
        .filter('comma2decimal', comma2decimal)
        .filter('decimal2comma', decimal2comma)
        .filter('dz', dz);



    /** @ngInject */
    function dzDirective($filter) {
        return {
            restrict: 'A',
            require: 'ngModel',
            link: function (scope, element, attrs, ngModelController) {

                ngModelController.$parsers.push(function (data) {
                    //convert data from view format to model format
                    data = $filter('comma2decimal')(data);
                    return data;
                });

                ngModelController.$formatters.push(function (data) {
                    //convert data from model format to view format
                    data = $filter('decimal2comma')(data);
                    return data;
                });
            }
        };
    }
    // parser
    function comma2decimal() {

        return function (input) {

            var ret = input ? input.toString().trim().replace(",", ".") : input.toString();
            if (ret.length < 1 || isNaN(ret)) {
                alert('NUMAI NUMERE!');
                return '';
            }
            return parseFloat(ret);
        };
    }

    // formatter
    function decimal2comma() {

        return function (input) {
            if (isNaN(input) || input == null) {
                return null;
            }
            // transform point to comma
            var ret = input ? input.toString().replace(".", ",") : input.toString();
            // deal with non-numbers


            // always show 2 decimals (formatter)
            if (ret) {
                var decArr = ret.split(",");
                if (decArr.length > 1) {
                    var decimals = decArr[1].length;

                    if (decimals === 1) {
                        ret += "0";
                    } // input value or model has 1 decimals
                    if (decimals === 2) {
                        ret += "";
                    } // input value or model has 2 decimals
                } else {
                    ret += ",00"; // input value or model has no decimal
                }
            }

            return ret;
        };
    }

    function dz() {

        return function (input) {

            if (isNaN(input) || input == null || typeof input === 'undefined') {
                return '';
            }
            // transform point to dot
            var r = input ? Math.round(input, 2).toString().replace(".", ",") : input.toString();
            // deal with non-numbers


            // always show 2 decimals (formatter)
            if (r) {
                var decArr = r.split(",");
                if (decArr.length > 1) {
                    var decimals = decArr[1].length;
                    if (decimals === 0) {
                        r += "00"
                    }
                    if (decimals === 1) {
                        r += "0";
                    } // input value or model has 1 decimals
                    if (decimals === 2) {
                        r += "";
                    } // input value or model has 2 decimals
                } else {
                    r += ",00"; // input value or model has no decimal
                }
            }

            return r;
        };
    }



})();
if (typeof jQuery !== "undefined" && typeof saveAs !== "undefined") {
    (function($) {
        $.fn.wordExport = function(fileName, styles) {
            fileName = typeof fileName !== 'undefined' ? fileName : "jQuery-Word-Export";
            var staticc = {
                mhtml: {
                    top: "Mime-Version: 1.0\nContent-Base: " + location.href + "\nContent-Type: Multipart/related; boundary=\"NEXT.ITEM-BOUNDARY\";type=\"text/html\"\n\n--NEXT.ITEM-BOUNDARY\nContent-Type: text/html; charset=\"utf-8\"\nContent-Location: " + location.href + "\n\n<!DOCTYPE html>\n<html>\n_html_</html>",
                    head: "<head>\n<meta http-equiv=\"Content-Type\" content=\"text/html; charset=utf-8\">\n<style>\n_styles_\n</style>\n</head>\n",
                    body: "<body>_body_</body>"
                }
            };

            var options = {
                maxWidth: 624
            };
            // Clone selected element before manipulating it
            var markup = $(this).clone();

            // Remove hidden elements from the output
            markup.each(function() {
                var self = $(this);
                if (self.is(':hidden'))
                    self.remove();
            });

            // Embed all images using Data URLs
            var images = Array();
            var img = markup.find('img');
            for (var i = 0; i < img.length; i++) {
                // Calculate dimensions of output image
                var w = Math.min(img[i].width, options.maxWidth);
                var h = img[i].height * (w / img[i].width);
                // Create canvas for converting image to data URL
                var canvas = document.createElement("CANVAS");
                canvas.width = w;
                canvas.height = h;
                // Draw image to canvas
                var context = canvas.getContext('2d');
                context.drawImage(img[i], 0, 0, w, h);
                // Get data URL encoding of image
                var uri = canvas.toDataURL("image/png");
                $(img[i]).attr("src", img[i].src);
                img[i].width = w;
                img[i].height = h;
                // Save encoded image to array
                images[i] = {
                    type: uri.substring(uri.indexOf(":") + 1, uri.indexOf(";")),
                    encoding: uri.substring(uri.indexOf(";") + 1, uri.indexOf(",")),
                    location: $(img[i]).attr("src"),
                    data: uri.substring(uri.indexOf(",") + 1)
                };
            }

            // Prepare bottom of mhtml file with image data
            var mhtmlBottom = "\n";
            for (var i = 0; i < images.length; i++) {
                mhtmlBottom += "--NEXT.ITEM-BOUNDARY\n";
                mhtmlBottom += "Content-Location: " + images[i].location + "\n";
                mhtmlBottom += "Content-Type: " + images[i].type + "\n";
                mhtmlBottom += "Content-Transfer-Encoding: " + images[i].encoding + "\n\n";
                mhtmlBottom += images[i].data + "\n\n";
            }
            mhtmlBottom += "--NEXT.ITEM-BOUNDARY--";

            //TODO: load css from included stylesheet
            var styles = styles;

            // Aggregate parts of the file together
            var fileContent = staticc.mhtml.top.replace("_html_", staticc.mhtml.head.replace("_styles_", styles) + staticc.mhtml.body.replace("_body_", markup.html())) + mhtmlBottom;
            console.log("fileContent:",markup);
            // Create a Blob with the file contents
            var blob = new Blob([fileContent], {
                type: "application/msword;charset=utf-8"
            });
            saveAs(blob, fileName + ".doc");
        };
    })(jQuery);
} else {
    if (typeof jQuery === "undefined") {
        console.error("jQuery Word Export: missing dependency (jQuery)");
    }
    if (typeof saveAs === "undefined") {
        console.error("jQuery Word Export: missing dependency (FileSaver.js)");
    }
}

